import React, {useEffect, useState} from 'react';
import classnames from "classnames";
import styles from "../LoginPage/style.module.css";
import axios from "axios";
import {
    Checkbox,
    FormControl,
    InputLabel,
    ListItemText,
    MenuItem,
    OutlinedInput,
    Select,
    TextField
} from "@mui/material";

function CreateTaskPage(props) {

    const [type_, setType] = useState("")

    const [students, setStudents] = useState([])

    useEffect(() => {
        axios.get("/api/students")
            .then((response) => {
                setStudents(response.data.users)
            })
    }, [])

    const [people, setPeople] = useState([])

    const [open, setOpen] = useState(false)
    const [data, setData] = useState({
        status: "info",
        text: "  "
    })

    return (
        <main className={classnames(styles.main)}>
            <form className={classnames(styles.form)} name="task_form" onSubmit={(event) => {
                event.preventDefault();
                const form = document.forms["task_form"]
                setData({
                    status: "info",
                    text: "  "
                })
                setOpen(true)
                axios.post("/api/task", {
                    title: form.title.value,
                    description: form.description.value,
                    type: type_,
                    people_id: people
                })
                    .then((response) => {
                        if (response.data.status) {
                            setData({
                                status: "success",
                                text: ""
                            })
                        }
                    })
                    .catch((response) => {
                        setData({
                            status: "error",
                            text: ""
                        })
                    })
            }}>
                <FormControl>
                    <InputLabel required={true} htmlFor="title"></InputLabel>
                    <OutlinedInput required={true} label="" id="title" name="title"/>
                </FormControl>
                <FormControl>
                    <TextField required={true} label="" id="description" name="description"/>
                </FormControl>
                <FormControl>
                    <InputLabel required={true} htmlFor="title"> </InputLabel>
                    <Select required={true} label=" " value={type_} id="type" name="type"
                            onChange={(event) => {
                                setType(event.target.value)
                            }}>
                        <MenuItem key={1} value=""></MenuItem>
                        <MenuItem key={2} value=""></MenuItem>
                        <MenuItem key={3} value=""></MenuItem>
                        <MenuItem key={4} value=""></MenuItem>
                    </Select>
                </FormControl>
                <FormControl>
                    <InputLabel></InputLabel>
                    <Select
                        labelId="people-label"
                        id="people"
                        multiple
                        value={people}
                        onChange={(event) => {
                            const {
                                target: {value},
                            } = event;
                            setPeople(typeof value === 'string' ? value.split(',') : value,)
                        }}
                        input={<OutlinedInput label=""/>}
                        renderValue={(selected) => selected.join(', ')
                        }
                    >
                        {students.map((student) => (
                            <MenuItem key={student.id} value={student.id}>
                                <Checkbox checked={people.indexOf(student.id) > -1}/>
                                <ListItemText primary={student.name + " " + student.surname}/>
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <FormControl>
                    <OutlinedInput value="" type="submit"/>
                </FormControl>
            </form>
        </main>
    );
}

export default CreateTaskPage;